    getCityes(){
        let new_iller=[]         
        for(let i=0;i<Object.keys(this.iller).length;i++){
          new_iller[this.iller[i].id] = this.iller[i].name
        }

        new_iller = new_iller.filter((il,key)=>{
            if(key<100) return il;
        })

        new_iller = new_iller.map((il,key)=>{ 
            let obj         = {};
            obj["id"]       = key+1;
            obj["name"]     = il
            return obj
        })  
        return new_iller;
    }

    getTowns(il_id:string){
        let new_ilceler=[]  

        new_ilceler = this.ilceler.filter((il,key)=>{ 
            if(il_id == il.il_id){
                return il
            }
        })  
        return new_ilceler;
    }
